import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SharedService } from '../../shared/shared.service';
import { NgbModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
// import { OAuthService } from 'angular-oauth2-oidc';
import { DashboardService } from '../dashboard.service';
import { Auth } from 'aws-amplify';
import { forkJoin } from 'rxjs';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {

  blueNumber: string = '';
  profileInfo: any;
  isTruncated: boolean = true;
  isSSIEnabled: boolean = false;


  constructor(private dashboardService: DashboardService, private sharedService: SharedService,
    private router: Router, private modalService: NgbModal, config: NgbModalConfig
  ) {
    config.backdrop = 'static';
    config.keyboard = false;
  }

  ngOnInit() {
    this.blueNumber = localStorage.getItem('personBluenumber')!;
    this.sharedService.startLoader();
    forkJoin([this.dashboardService.getProfileDetails(this.blueNumber), this.dashboardService.checkSSICredentials()])
      .subscribe(([profileResponse, checkSSIResponse]) => {
        this.sharedService.stopLoader();
        if (!profileResponse['isError']) {
          this.profileInfo = profileResponse.result;
        }
        this.isSSIEnabled = checkSSIResponse['result'];
      });
  }
  onBackClick() {
    this.router.navigateByUrl('home/info');
  }

  onLogoutClick(content: any) {
    this.modalService.open(content, {
      size: 'sm modal-dialog-centered',
    });
  }
  onUserConfirm(type: string) {
    if (type == 'yes') {
      localStorage.clear();
      //this.oauthService.logOut();
      Auth.signOut();
      this.router.navigateByUrl('auth/home')
    }
    this.modalService.dismissAll();
  }
  closePopup() {
    this.modalService.dismissAll();
  }
}
