import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AcceptanceScreenComponent } from './acceptance-screen/acceptance-screen.component';
import { DashboardLandingPageComponent } from './dashboard-landing-page/dashboard-landing-page.component';
import { DashboardLayoutComponent } from './dashboard-layout/dashboard-layout.component';
import { LanguageComponent } from './language/language.component';
import { MessagesComponent } from './messages/messages.component';
import { ProfileComponent } from './profile/profile.component';
import { SurveyQuestionsComponent } from './survey-questions/survey-questions.component';
import { BankIfscComponent } from './bank-ifsc/bank-ifsc.component';
import { SurveyOdkQuestionsComponent } from './survey-odk-questions/survey-odk-questions.component';
import { UpiDetailsComponent } from './upi-details/upi-details.component';

const routes: Routes = [
  {
    path: '',
    component: DashboardLayoutComponent,
    children: [
      {
        path: '',
        redirectTo: 'info',
        pathMatch: 'full'
      },
      {
        path: 'info',
        component: DashboardLandingPageComponent
      },
      {
        path: 'language',
        component: LanguageComponent
      },
      {
        path: 'accept/:orgName',
        component: AcceptanceScreenComponent
      },
      {
        path: 'survey/:id',
        component: SurveyQuestionsComponent
      },
      {
        path: 'survey/odk/:id',
        component: SurveyOdkQuestionsComponent
      },
      {
        path: 'profile',
        component: ProfileComponent
      },
      {
        path: 'messages',
        component: MessagesComponent
      },
      {
        path: 'bank-details',
        component: BankIfscComponent
      },
      {
        path: 'upi-details',
        component: UpiDetailsComponent,
      },      
      {
        path: '**',
        redirectTo: 'list',
        pathMatch: 'full'
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DashboardRoutingModule { }
