import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { Renderer2 } from '@angular/core';
import { BankIfscService } from '../bankifsc.service';
import { SharedService } from 'src/app/shared/shared.service';
import { SurveyService } from '../survey.service';


export interface State {
  name: string;
}

export interface IfscAddressGroup {
  ifsc: string;
  address: string[];
}

export interface BankDetails {
  bank?: string;
  state?: string;
  city1?: string;
  city2?: string;
  ifsc?: string;
  branch?: string;
  address?: string;
}

@Component({
  selector: 'app-bank-ifsc',
  templateUrl: './bank-ifsc.component.html',
  styleUrls: ['./bank-ifsc.component.scss']
})

export class BankIfscComponent {

  ifsc: string = '';
  address: string = '';
  ifscAddressGroup: IfscAddressGroup[] = [];
  selectedOption: string = '';
  bankDetails: BankDetails = {
    bank: '',
    state: '',
    city1: '',
    city2: '',
    ifsc: '',
    branch: '',
    address: ''
  };

  stateForm: FormGroup = this._formBuilder.group({
    stateGroup: '',
  });

  ifscAddressGroupOptions: Observable<IfscAddressGroup[]> | undefined;

  title = 'sample-app';
  floatLabelControl = new FormControl('auto');

  bankControl = new FormControl('', [Validators.required]);
  stateControl = new FormControl('', [Validators.required]);
  cityControl = new FormControl('', [Validators.required]);
  branchControl = new FormControl('', [Validators.required]);
  stateGroup = new FormControl('', [Validators.required]);
  radioButtonValue: number = 0;
  ifscControl = new FormControl('', [Validators.required, Validators.minLength(11), Validators.maxLength(11)]);
  accountNumberControl = new FormControl('', [Validators.required, Validators.minLength(8)]);
  retypeAccountNumberControl = new FormControl('', [Validators.required]);


  banks: string[] = []; // ['One', 'Two', 'Three'];
  states: string[] = [];
  cities: string[] = [];
  branches: string[] = [];
  ifscAndAddress: IfscAddressGroup[] = [];

  filteredBanks: Observable<string[]> | undefined;
  filteredStates: Observable<string[]> | undefined;
  filteredCities: Observable<string[]> | undefined;
  filteredBranches: Observable<string[]> | undefined;

  private _filterBanks(value: string): string[] {
    const filterValue = value?.toLowerCase();
    return this.banks.filter(bank => bank.toLowerCase().includes(filterValue));
  }

  private _filter2(value: string): string[] {
    const filterValue = value?.toLowerCase();

    return this.states.filter(state => state.toLowerCase().includes(filterValue));
  }

  private _filterCity(value: string): string[] {
    const filterValue = value?.toLowerCase();

    return this.cities.filter(option => option.toLowerCase().includes(filterValue));
  }

  private _filterBranch(value: string): string[] {
    const filterValue = value?.toLowerCase();

    return this.branches.filter(option => option.toLowerCase().includes(filterValue));
  }

  private _filterAddress = (opt: string[], value: string): string[] => {
    const filterValue = value?.toLowerCase();

    return opt.filter(item => item.toLowerCase().includes(filterValue));
  };

  private _filterGroup(value: string): IfscAddressGroup[] {
    if (value) {
      return this.ifscAddressGroup
        .map(group => ({ ifsc: group.ifsc, address: this._filterAddress(group.address, value) }))
        .filter(group => group.address.length > 0);
    }
    return this.ifscAddressGroup;
  }

  onBankdetailsSubmit() {
    let ifsc = '';
    this.accountNumberControl.markAllAsTouched();
    this.retypeAccountNumberControl.markAllAsTouched();

    if (this.selectedOption == 'ifsccode') {
      ifsc = this.ifscControl.value;
    } else if (this.selectedOption == 'noifsccode') {
      ifsc = this.ifsc;
      this.bankControl.markAllAsTouched();
      this.stateControl.markAllAsTouched();
      this.cityControl.markAllAsTouched();
      this.branchControl.markAllAsTouched();
      this.stateGroup.markAllAsTouched();
    }
    // **** Check Account No is Valid or not ***********
    if (this.accountNumberControl.invalid || this.retypeAccountNumberControl.invalid) { return; }
    if (this.accountNumberControl.value !== this.retypeAccountNumberControl.value) {
      this.sharedService.showToastMesaages('error', 'Account number not matched');
      return;
    } else {
      // *********** If Ifsc Code is present Save Bank Data *****************
      if (this.selectedOption == 'ifsccode') {
        this.saveBankDetails(ifsc);
      } else if (this.selectedOption == 'noifsccode') {
        // *********** Double check with Timeout for Bank Details Validity beacause of Autocomplete Fields *********************
        if (this.checkBankDetailsValidity()) {
          this.sharedService.startLoader();
          setTimeout(() => {
            this.sharedService.stopLoader();
            if (this.checkBankDetailsValidity()) {
              this.saveBankDetails(ifsc);
            }
          }, 1000);
        }
      }
    }
  }
  // ********* Returns True / False based on Bank details **********************
  checkBankDetailsValidity() {
    return this.bankControl.valid && this.stateControl.valid && this.cityControl.valid
      && this.branchControl.valid && this.stateGroup.valid;
  }
  // *********** API to save Bak Details ****************
  saveBankDetails(ifsc: any) {
    this.sharedService.startLoader();
    this.bankifscService.saveUserDetails({
      ifscCode: ifsc,
      surveySetId: localStorage.getItem('surveySetId'),
      bankAccountNumber: this.accountNumberControl.value
    }).subscribe((res: any) => {
      if (!res['isError']) {
        this.sharedService.showToastMesaages('success', 'User bank details saved successfully');
        this.router.navigateByUrl('home/info');
      }
      this.sharedService.stopLoader();
      //redirect to specified page
    });
  }

  constructor(private router: Router, private bankifscService: BankIfscService, private renderer: Renderer2,
    private _formBuilder: FormBuilder, private sharedService: SharedService, private surveyService: SurveyService) {

  }

  ngOnInit() {
    if (localStorage.getItem('ssid') != null && localStorage.getItem('ssid') != 'null' && localStorage.getItem('ssid') != '') {
      this.fetchSurveySetSSIDDetails();
    }
  }
  // ********** Method to fetch the Survey Info based on SSID  ************************
  fetchSurveySetSSIDDetails() {
    this.sharedService.startLoader();
    this.surveyService.getSurveyGroupByQRCode(localStorage.getItem('ssid')).subscribe(res => {
      this.sharedService.stopLoader();
      if (!res['isError']) {
        if (res['result']['isBankDetailsExist']) {
          this.sharedService.showToastMesaages('info', 'Bank details already exist');
          this.router.navigateByUrl('home/info');
        }
      }
    });
  }

  onVerify() {
    this.sharedService.startLoader();
    this.bankifscService.getBankDetails(this.ifscControl.value).subscribe((res: any) => {
      this.sharedService.stopLoader();
      if (!res['isError']) {
        this.bankDetails = res.result;
      }
    }, (error => {
      this.sharedService.showToastMesaages('error', 'Enter valid IFSC Code');
    }));
  }

  onSubmit() {
    if (this.radioButtonValue == 2) {
      // ******** Reset IFSC, Bank Details of i have IFSC Code Option ******************
      this.ifscControl.reset();
      this.bankDetails = {};

      this.sharedService.startLoader();
      this.selectedOption = 'noifsccode';
      this.bankifscService.getBankNames().subscribe((data: any) => {
        this.bankControl.setValue('');
        this.clearValues("STATE", "CITY", "BRANCH", "IFSC", "ACCOUNT_NUMBER");
        this.banks = data.result.names;
        //state filtered options     
        this.sharedService.stopLoader();
        this.filteredBanks = this.bankControl.valueChanges.pipe(
          startWith(''),
          map(value => this._filterBanks(value)),
        );
      });
    } else if (this.radioButtonValue == 1) {
      this.selectedOption = 'ifsccode';
      // ******** Reset I Dont have IFSC Code Option Data ******************
      this.clearValues("BANK", "STATE", "CITY", "BRANCH", "IFSC", "ACCOUNT_NUMBER");
    }
    this.accountNumberControl.reset();
    this.retypeAccountNumberControl.reset();
  }

  // get states bases on bank
  getStates() {
    this.sharedService.startLoader();
    this.bankifscService.getStateNames(this.bankControl.value).subscribe((data: any) => {
      this.stateControl.setValue('');
      this.states = data.result.state;
      //state filtered options     
      this.clearValues("CITY", "BRANCH", "IFSC", "ACCOUNT_NUMBER");
      this.sharedService.stopLoader();
      this.filteredStates = this.stateControl.valueChanges.pipe(
        startWith(''),
        map(value => this._filter2(value)),
      );
    });
  }

  // get cities bases on bank and state
  getCities() {
    this.sharedService.startLoader();
    this.bankifscService.getCityNames(this.bankControl.value, this.stateControl.value).subscribe((data: any) => {
      this.cityControl.setValue('');
      this.cities = data.result.cities;

      //state filtered options     
      this.clearValues("BRANCH", "IFSC", "ACCOUNT_NUMBER");
      this.sharedService.stopLoader();
      this.filteredCities = this.cityControl.valueChanges.pipe(
        startWith(''),
        map(value => this._filterCity(value)),
      );
    });
  }

  //get branches based on bank, state and city
  getBranches() {
    this.sharedService.startLoader();
    this.bankifscService.getBranchNames(this.bankControl.value, this.stateControl.value, this.cityControl.value).subscribe((data: any) => {
      this.branchControl.setValue('');
      this.branches = data.result.branches;

      //state filtered options     
      this.clearValues("IFSC", "ACCOUNT_NUMBER");
      this.sharedService.stopLoader();
      this.filteredBranches = this.branchControl.valueChanges.pipe(
        startWith(''),
        map(value => this._filterBranch(value)),
      );
    });
  }

  // get ifsc and address based on bank, state, city and branch
  getIFSCAndAddressList() {

    this.sharedService.startLoader();
    this.bankifscService.getIfscAndAddress(
      this.bankControl.value, this.stateControl.value, this.cityControl.value, this.branchControl.value
    ).subscribe((data: any) => {
      this.stateGroup.setValue('');
      this.clearValues("IFSC");
      this.ifscAddressGroup = data.result;
      this.sharedService.stopLoader();
      this.ifscAddressGroup = data.result.map((item: any) => ({
        ifsc: item.ifsc,
        address: [item.address]
      }));

      this.ifscAddressGroupOptions = this.stateForm.get('stateGroup')!.valueChanges.pipe(
        startWith(''),
        map(value => this._filterGroup(value)),
      );
    });
  }


  // get ifsc and address On ifsc select
  getSelectedIFSCAddress() {
    this.ifscAddressGroupOptions?.subscribe(res => {
      let data = res.filter(i => i.address[0] == this.stateGroup.value);
      if (data && data.length > 0) {
        this.ifsc = data[0]['ifsc'];
        this.address = this.stateGroup.value;
      }
    });
  }


  onBackClick() {
    this.router.navigateByUrl('/home');
  }

  clearValues(...args: any[]) {
    args.forEach(arg => {
      switch (arg) {
        case "BANK":
          this.bankControl.setValue('');
          this.banks = [];
          this.bankControl.reset();
          break;
        case "STATE":
          this.stateControl.setValue('');
          this.states = [];
          this.stateControl.reset();
          break;
        case "CITY":
          this.cityControl.setValue('');
          this.cities = [];
          this.cityControl.reset();
          break;
        case "BRANCH":
          this.branchControl.setValue('');
          this.branches = [];
          this.branchControl.reset();
          break;
        case "IFSC":
          this.stateGroup.setValue('');
          this.ifsc = '';
          this.address = '';
          this.stateGroup.reset();
          break;
      }
    });
  }

  // ********** Triggers when text is entered in the Autocomplete field without selection *******************
  onAutoCompleteInputChange(event: any, type: string, filterArray: any) {
    setTimeout(() => {
      let nameEntered = event.target.value;
      let selectedName: any;
      if (nameEntered != '') {
        selectedName = filterArray.filter((data: any) => data.toLowerCase() == nameEntered.toLowerCase());
        // ********* If enetered value is not present clear the field and dependent field Values ****************
        if (selectedName.length == 0) {
          switch (type) {
            case 'bank':
              this.bankControl.reset();
              this.clearValues("STATE", "CITY", "BRANCH", "IFSC");
              break;
            case 'state':
              this.stateControl.reset();
              this.clearValues("CITY", "BRANCH", "IFSC");
              break;
            case 'city':
              this.cityControl.reset();
              this.clearValues("BRANCH", "IFSC");
              break;
            case 'branch':
              this.branchControl.reset();
              this.clearValues("IFSC");
              break;
            case 'ifsc':
              this.stateGroup.reset();
              this.ifsc = '';
              this.address = '';
              break;
          }
        } else {
          // ************** If Entered Value is present in the filtered list the call the dependency APIs *******************
          switch (type) {
            case 'bank':
              this.getStates();
              break;
            case 'state':
              this.getCities();
              break;
            case 'city':
              this.getBranches();
              break;
            case 'branch':
              this.getIFSCAndAddressList();
              break;
            case 'ifsc':
              this.getSelectedIFSCAddress();
              break;
          }
        }
      }
      //  else {
      //   this.bankControl.reset();
      // }
    }, 1000);
  }

}