import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { HttpParams } from '@angular/common/http';

@Injectable({
    providedIn: 'root'
})
export class BankIfscService {

    constructor(private http: HttpClient) { }

    getBankNames(): Observable<any> {
        return this.http.get(`${environment.BlueNumberGlobalAPI}/api/v1/Banks/GetBankNames`);
    }

    getStateNames(bankName: string): Observable<any> {       
        const params =  new HttpParams().set('bank', bankName);
        return this.http.get(`${environment.BlueNumberGlobalAPI}/api/v1/Banks/GetStateNamesByBank`, {params});        
    }

    getCityNames(bankName: string, stateName: string): Observable<any> {
        const params =  new HttpParams().set('bank', bankName).set('state', stateName);
        return this.http.get(`${environment.BlueNumberGlobalAPI}/api/v1/Banks/GetCityNameByBankAndState`, {params});        
    }

    getBranchNames(bankName: string, stateName: string, cityName: string): Observable<any> {
        const params =  new HttpParams().set('bank', bankName).set('state', stateName).set('city', cityName);
        return this.http.get(`${environment.BlueNumberGlobalAPI}/api/v1/Banks/GetBranchesByBankStateCity`, {params});        
    }

    getIfscAndAddress(bankName: string, stateName: string, cityName: string, branchName: string): Observable<any> {
        const params =  new HttpParams().set('bank', bankName).set('state', stateName).set('city', cityName).set('branch', branchName);
        return this.http.get(`${environment.BlueNumberGlobalAPI}/api/v1/Banks/GetIfscAddressByBankStateCityBranch`, {params});        
    }

    getBankDetails(ifsc: string): Observable<any> {
        const params =  new HttpParams().set('ifsc', ifsc);
        return this.http.get(`${environment.BlueNumberGlobalAPI}/api/v1/Banks/GetBankStateCityBranchByIfsc`, {params});        
    } 

    saveUserDetails(params: any): Observable<any> {
        return this.http.post(`${environment.BlueNumberGlobalAPI}/api/v1/Banks/SaveUserDetails`, params);
    }
}
